import { useState } from "react";
import { ethers } from "ethers";

const CONTRACT_ADDRESS = "0x39Eb022Aea80ffc165d72Be7fE93dcd8B5418B3B";
const ABI = [
  "function commitEggFight(address versus) public",
  "function revealEggFight(address challenger) public",
  "function wins(address) view returns (uint)",
  "function losses(address) view returns (uint)",
  "function draws(address) view returns (uint)"
];

async function switchToPolygon() {
  console.log("switch to polygon");
  const polygonChainId = "0x89";

  try {
    await window.ethereum.request({
      method: "wallet_switchEthereumChain",
      params: [{ chainId: polygonChainId }],
    });
  } catch (switchError) {
    if (switchError.code === 4902) {
      await window.ethereum.request({
        method: "wallet_addEthereumChain",
        params: [
          {
            chainId: polygonChainId,
            chainName: "Polygon Mainnet",
            rpcUrls: ["https://polygon-rpc.com"],
            nativeCurrency: {
              name: "MATIC",
              symbol: "MATIC",
              decimals: 18,
            },
            blockExplorerUrls: ["https://polygonscan.com"],
          },
        ],
      });
    } else {
      console.error(switchError);
    }
  }
}

window['switchToPolygon'] = switchToPolygon;

function App() {
  const [account, setAccount] = useState(null);
  const [opponent, setOpponent] = useState("");
  const [result, setResult] = useState("");
  const [stats, setStats] = useState(null);

  async function connect() {
    if (!window.ethereum) {
      alert("Please install MetaMask or Rabby.");
      return;
    }

    const [selected] = await window.ethereum.request({ method: "eth_requestAccounts" });
    setAccount(selected);

    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const contract = new ethers.Contract(CONTRACT_ADDRESS, ABI, signer);
    console.log(contract, contract.wins);

    const winsMe = await contract.wins(selected);
    const lossesMe = await contract.losses(selected);
    const drawsMe = await contract.draws(selected);

    setStats({
      me: { wins: winsMe.toString(), losses: lossesMe.toString(), draws: drawsMe.toString() },
    });
  }

  async function fight() {
    if (!account || !opponent) return;

    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const signer = provider.getSigner();
    const contract = new ethers.Contract(CONTRACT_ADDRESS, ABI, signer);

    setResult("Sending transaction...");

    try {
      const tx = await contract.commitEggFight(opponent);
      await tx.wait();

      setResult("Commiting...");

      const tx2 = await contract.revealEggFight(account);
      await tx2.wait();

      setResult("Battle completed!");

      // Read updated stats
      const winsMe = await contract.wins(account);
      const lossesMe = await contract.losses(account);
      const drawsMe = await contract.draws(account);

      const winsOp = await contract.wins(opponent);
      const lossesOp = await contract.losses(opponent);
      const drawsOp = await contract.draws(opponent);

      setStats({
        me: { wins: winsMe.toString(), losses: lossesMe.toString(), draws: drawsMe.toString() },
        opponent: { wins: winsOp.toString(), losses: lossesOp.toString(), draws: drawsOp.toString() },
      });
    } catch (err) {
      console.error(err);
      setResult("Error during fight: " + err.message);
    }
  }

  return (
    <div style={{ padding: 20, fontFamily: "sans-serif" }}>
      <h1> Paques Egg Fight</h1>
      {!account && <button onClick={connect}>Connect Wallet</button>}
      {account && (
        <>
          <p>Your address: {account}</p>
          <input
            type="text"
            placeholder="Opponent address"
            value={opponent}
            onChange={(e) => setOpponent(e.target.value)}
            style={{ width: "400px" }}
          />
          <br />
          <button onClick={fight} style={{ marginTop: 10 }}>Fight!</button>
          <p>{result}</p>

          {stats && (
            <div style={{ marginTop: 20 }}>
              <h3>Stats:</h3>
              <table border="1" cellPadding="8">
                <thead>
                  <tr>
                    <th></th>
                    <th>Wins</th>
                    <th>Losses</th>
                    <th>Draws</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>You</td>
                    <td>{stats.me.wins}</td>
                    <td>{stats.me.losses}</td>
                    <td>{stats.me.draws}</td>
                  </tr>
                  { stats.opponent && (
                      <tr>
                        <td>Opponent</td>
                        <td>{stats.opponent.wins}</td>
                        <td>{stats.opponent.losses}</td>
                        <td>{stats.opponent.draws}</td>
                      </tr>
                  )}
                </tbody>
              </table>
            </div>
          )}
        </>
      )}
    </div>
  );
}

export default App;

